/**
 * WordPress dependencies
 */
import * as React from '@wordpress/element';
import { TextControl } from '@wordpress/components';
import { useSelect } from '@wordpress/data';
import { Fragment } from '@wordpress/element';
import { _x, sprintf } from '@wordpress/i18n';

/**
 * External dependencies
 */
import { trim } from 'lodash';
import interpolateComponents from '@automattic/interpolate-components';
import { STORE_NAME as NAB_DATA } from '@nab/data';
import type { CAEditProps } from '@nab/types';

/**
 * Internal dependencies
 */
import type { Attributes } from './types';

export const Edit = ( {
	attributes: { eventName },
	experimentId,
	goalIndex,
	setAttributes,
}: CAEditProps< Attributes > ): JSX.Element => {
	const trackingScript = useTrackingScript();
	const jsAction = !! trim( eventName )
		? `nab.{{wbr /}}trigger({{wbr /}}'${ trim( eventName ) }')`
		: `nab.{{wbr /}}convert({{wbr /}}${ experimentId },{{wbr /}}${ goalIndex })`;

	return (
		<Fragment>
			<p>
				{ _x(
					'You can programmatically generate a conversion using the following JavaScript snippet whenever you want:',
					'user',
					'nelio-ab-testing'
				) }
			</p>

			<pre>
				{ '<script type="text/javascript">\n  ' }
				{ interpolateComponents( {
					mixedString: jsAction,
					components: { wbr: <wbr /> },
				} ) }
				{ ';\n</script>' }
			</pre>

			<p>
				{ _x(
					'To generate the conversion on a third-party website, remember to include the tracking script before using the previous snippet:',
					'user',
					'nelio-ab-testing'
				) }
			</p>

			<pre>{ `<script\n  type="text/javascript"\n  src="${ trackingScript }">\n</script>` }</pre>

			<p>
				{ interpolateComponents( {
					mixedString: sprintf(
						/* translators: a JavaScript snippet */
						_x(
							'Finally, if you want a single custom event to trigger a conversion in several goals or different tests, please name the event and use %s instead:',
							'user',
							'nelio-ab-testing'
						),
						'{{code}}nab.trigger{{/code}}'
					),
					components: { code: <code /> },
				} ) }
			</p>
			<TextControl
				label={ _x( 'Custom Event Name', 'text', 'nelio-ab-testing' ) }
				placeholder={ _x( '(optional)', 'text', 'nelio-ab-testing' ) }
				value={ eventName || '' }
				onChange={ ( name ) => setAttributes( { eventName: name } ) }
			/>
		</Fragment>
	);
};

// =====
// HOOKS
// =====

const useTrackingScript = () =>
	useSelect( ( select ) =>
		select( NAB_DATA ).getPluginSetting( 'externalTrackingScript' )
	);
