/**
 * WordPress dependencies
 */
import * as React from '@wordpress/element';
import { useSelect } from '@wordpress/data';
import { Fragment } from '@wordpress/element';
import { sprintf, _x } from '@wordpress/i18n';

/**
 * External dependencies
 */
import interpolateComponents from '@automattic/interpolate-components';
import { STORE_NAME as NAB_DATA } from '@nab/data';
import type { CAViewProps } from '@nab/types';

/**
 * Internal dependencies
 */
import type { Attributes } from './types';

export const View = ( {
	attributes: { formType, formId },
}: CAViewProps< Attributes > ): JSX.Element => {
	const formTitle = useFormTitle( formType, formId );

	if ( ! formTitle ) {
		return <>{ getLabelForUnknownForm( formType ) }</>;
	} //end if

	return (
		<Fragment>
			{ interpolateComponents( {
				mixedString: sprintf(
					getLabelForKnownForm( formType ),
					`{{strong}}${ formTitle }{{/strong}}`
				),
				components: { strong: <strong /> },
			} ) }
		</Fragment>
	);
};

// =====
// HOOKS
// =====

const useFormTitle = ( formType, formId ) =>
	useSelect(
		( select ) =>
			select( NAB_DATA ).getEntityRecord( formType, formId )?.title
	);

// =======
// HELPERS
// =======

function getLabelForUnknownForm( formType ) {
	switch ( formType ) {
		case 'nelio_form':
			return _x(
				'A visitor successfully submits a Nelio Form.',
				'text',
				'nelio-ab-testing'
			);

		case 'wpcf7_contact_form':
			return _x(
				'A visitor successfully submits a Contact Form 7.',
				'text',
				'nelio-ab-testing'
			);

		case 'nab_gravity_form':
			return _x(
				'A visitor successfully submits a Gravity Form.',
				'text',
				'nelio-ab-testing'
			);

		case 'nab_ninja_form':
			return _x(
				'A visitor successfully submits a Ninja Form.',
				'text',
				'nelio-ab-testing'
			);

		case 'nab_formidable_form':
			return _x(
				'A visitor successfully submits a Formidable Form.',
				'text',
				'nelio-ab-testing'
			);

		case 'wpforms':
			return _x(
				'A visitor successfully submits a WPForm.',
				'text',
				'nelio-ab-testing'
			);

		default:
			return _x(
				'A visitor successfully submits a form.',
				'text',
				'nelio-ab-testing'
			);
	} //end formType
} //end getLabelForUnknownForm()

function getLabelForKnownForm( formType ) {
	switch ( formType ) {
		case 'nelio_form':
			/* translators: form name */
			return _x(
				'A visitor successfully submits the %s Nelio Form.',
				'text',
				'nelio-ab-testing'
			);

		case 'wpcf7_contact_form':
			/* translators: form name */
			return _x(
				'A visitor successfully submits the %s Contact Form 7.',
				'text',
				'nelio-ab-testing'
			);

		case 'nab_gravity_form':
			/* translators: form name */
			return _x(
				'A visitor successfully submits the %s Gravity Form.',
				'text',
				'nelio-ab-testing'
			);

		case 'nab_ninja_form':
			/* translators: form name */
			return _x(
				'A visitor successfully submits the %s Ninja Form.',
				'text',
				'nelio-ab-testing'
			);

		case 'nab_formidable_form':
			/* translators: form name */
			return _x(
				'A visitor successfully submits the %s Formidable Form.',
				'text',
				'nelio-ab-testing'
			);

		case 'wpforms':
			/* translators: form name */
			return _x(
				'A visitor successfully submits the %s WPForm.',
				'text',
				'nelio-ab-testing'
			);

		default:
			/* translators: form name */
			return _x(
				'A visitor successfully submits the %s form.',
				'text',
				'nelio-ab-testing'
			);
	} //end formType
} //end getLabelForKnownForm()
