/**
 * WordPress dependencies
 */
import * as React from '@wordpress/element';
import { SelectControl, BaseControl } from '@wordpress/components';
import { useInstanceId } from '@wordpress/compose';
import { useSelect } from '@wordpress/data';
import { Fragment } from '@wordpress/element';
import { _x } from '@wordpress/i18n';

/**
 * External dependencies
 */
import classnames from 'classnames';
import { sortBy } from 'lodash';
import { ErrorText, PostSearcher } from '@nab/components';
import { STORE_NAME as NAB_DATA } from '@nab/data';
import type { CAEditProps } from '@nab/types';

/**
 * Internal dependencies
 */
import type { Attributes } from './types';

export const Edit = ( {
	attributes: { formId, formType },
	setAttributes,
	errors,
}: CAEditProps< Attributes > ): JSX.Element => {
	const instanceId = useInstanceId( Edit );
	const formTypes = useFormTypes();
	const currentFormType = formType || formTypes[ 0 ]?.value;

	return (
		<Fragment>
			{ formTypes.length > 1 && (
				<SelectControl
					label={ _x( 'Form Type', 'text', 'nelio-ab-testing' ) }
					options={ formTypes }
					value={ currentFormType }
					onChange={ ( newFormType ) =>
						setAttributes( {
							formType: newFormType,
							formId: undefined,
						} )
					}
				/>
			) }

			<BaseControl
				id={ `nab-conversion-action__form-submission-${ instanceId }` }
				className={ classnames( {
					'nab-conversion-action__field--has-errors': errors.formId,
				} ) }
				label={ _x( 'Actual Form', 'text', 'nelio-ab-testing' ) }
				help={ <ErrorText value={ errors.formId } /> }
			>
				<PostSearcher
					id={ `nab-conversion-action__form-submission-${ instanceId }` }
					type={ currentFormType }
					perPage={ 10 }
					value={ formId }
					onChange={ ( newFormId ) =>
						setAttributes( {
							formId: newFormId,
							formType: currentFormType,
						} )
					}
				/>
			</BaseControl>
		</Fragment>
	);
};

// =====
// HOOKS
// =====

const useFormTypes = () =>
	useSelect( ( select ) => {
		const { getKindEntities } = select( NAB_DATA );

		const formTypes = ( getKindEntities() || [] )
			.filter( ( pt ) => 'form' === pt.kind )
			.map( ( pt ) => ( {
				value: pt.name,
				label: pt.labels.singular_name,
			} ) );
		return sortBy( formTypes, 'label' );
	} );
