/**
 * WordPress dependencies
 */
import * as React from '@wordpress/element';
import {
	BaseControl,
	ExternalLink,
	SelectControl,
} from '@wordpress/components';
import { useInstanceId } from '@wordpress/compose';
import { useSelect } from '@wordpress/data';
import { Fragment } from '@wordpress/element';
import { _x } from '@wordpress/i18n';

/**
 * External dependencies
 */
import classnames from 'classnames';
import { sortBy } from 'lodash';
import interpolateComponents from '@automattic/interpolate-components';
import { ErrorText, PostSearcher } from '@nab/components';
import { STORE_NAME as NAB_DATA } from '@nab/data';
import type { CAEditProps } from '@nab/types';

/**
 * Internal dependencies
 */
import type { Attributes } from './types';

export const Edit = ( {
	attributes: { postId, postType },
	setAttributes,
	errors,
}: CAEditProps< Attributes > ): JSX.Element => {
	const instanceId = useInstanceId( Edit );
	const postTypes = usePostTypes();
	const currentPostType = postType || postTypes[ 0 ]?.value;

	return (
		<Fragment>
			{ postTypes.length > 1 && (
				<SelectControl
					label={ _x( 'Content Type', 'text', 'nelio-ab-testing' ) }
					options={ postTypes }
					value={ currentPostType }
					onChange={ ( newPostType ) =>
						setAttributes( {
							postType: newPostType,
							postId: undefined,
						} )
					}
				/>
			) }

			<BaseControl
				id={ `nab-conversion-action__page-view-${ instanceId }` }
				className={ classnames( {
					'nab-conversion-action__field--has-errors': !! errors.postId,
				} ) }
				label={ <Label postType={ postType } postId={ postId } /> }
				help={ <ErrorText value={ errors.postId } /> }
			>
				<PostSearcher
					id={ `nab-conversion-action__page-view-${ instanceId }` }
					type={ currentPostType }
					perPage={ 10 }
					value={ postId }
					onChange={ ( newPostId ) =>
						setAttributes( {
							postId: newPostId,
							postType: currentPostType,
						} )
					}
				/>
			</BaseControl>
		</Fragment>
	);
};

const Label = ( { postType, postId } ) => {
	const permalink = usePostPermalink( postType, postId );

	if ( ! permalink ) {
		return (
			<span>{ _x( 'Actual Content', 'text', 'nelio-ab-testing' ) }</span>
		);
	} //end if

	return (
		<span>
			{ interpolateComponents( {
				mixedString: _x(
					'Actual Content ({{a}}View{{/a}})',
					'text',
					'nelio-ab-testing'
				),
				components: {
					a: permalink ? (
						<ExternalLink href={ permalink } />
					) : (
						<span />
					),
				},
			} ) }
		</span>
	);
};

// =====
// HOOKS
// =====

const usePostTypes = () =>
	useSelect( ( select ) => {
		const { getKindEntities } = select( NAB_DATA );

		const postTypes = ( getKindEntities() || [] )
			.filter(
				( pt ) =>
					'entity' === pt.kind &&
					! [ 'attachment' ].includes( pt.name )
			)
			.map( ( pt ) => ( {
				value: pt.name,
				label: pt.labels.singular_name,
			} ) );
		return sortBy( postTypes, 'label' );
	} );

const usePostPermalink = ( postType, postId ) =>
	useSelect(
		( select ) =>
			select( NAB_DATA ).getEntityRecord( postType, postId )?.link
	);
