/**
 * WordPress dependencies
 */
import * as React from '@wordpress/element';
import { useSelect } from '@wordpress/data';
import { sprintf, _x } from '@wordpress/i18n';

/**
 * External dependencies
 */
import interpolateComponents from '@automattic/interpolate-components';
import { find } from 'lodash';
import { STORE_NAME as NAB_DATA } from '@nab/data';
import type {
	CAViewProps,
	ProductId,
	WooCommerceOrderStatusName,
} from '@nab/types';

/**
 * Internal dependencies
 */
import type { Attributes } from './types';

export const View = ( {
	attributes: { anyProduct, productId },
	goal: { orderStatusForConversion = 'wc-completed' },
}: CAViewProps< Attributes > ): JSX.Element => {
	const productTitle = useProductTitle( productId );
	const labels = useStatusLabels( orderStatusForConversion );

	if ( anyProduct ) {
		return <>{ labels.anyProduct }</>;
	} //end if

	if ( ! productTitle ) {
		return <>{ labels.unknownProduct }</>;
	} //end if

	return (
		<>
			{ interpolateComponents( {
				// eslint-disable-next-line @wordpress/valid-sprintf
				mixedString: sprintf(
					labels.knownProduct,
					sprintf( '{{strong}}%s{{/strong}}', productTitle )
				),
				components: { strong: <strong /> },
			} ) }
		</>
	);
};

// =====
// HOOKS
// =====

const useProductTitle = ( productId: ProductId ) =>
	useSelect(
		( select ) =>
			select( NAB_DATA ).getEntityRecord( 'product', productId )?.title
	);

type Labels = {
	readonly anyProduct: string;
	readonly unknownProduct: string;
	readonly knownProduct: string;
};

function useStatusLabels( status: WooCommerceOrderStatusName ): Labels {
	const statusName = useSelect(
		( select ) =>
			find( select( NAB_DATA ).getWooCommerceSetting( 'orderStatuses' ), {
				value: status,
			} )?.label ??
			_x( 'Unknown', 'text (wc order status)', 'nelio-ab-testing' )
	);

	switch ( status ) {
		case 'wc-completed':
			return {
				anyProduct: _x(
					'An order is completed.',
					'text',
					'nelio-ab-testing'
				),
				unknownProduct: _x(
					'An order containing a specific product is completed.',
					'text',
					'nelio-ab-testing'
				),
				/* translators: the name of a product */
				knownProduct: _x(
					'An order with the product %s is completed.',
					'text',
					'nelio-ab-testing'
				),
			};

		default:
			return {
				anyProduct: sprintf(
					/* translators: woocommerce status */
					_x(
						'The status of an order is set to %s.',
						'text',
						'nelio-ab-testing'
					),
					statusName
				),
				unknownProduct: sprintf(
					/* translators: woocommerce status */
					_x(
						'The status of an order with a specific product is set to %s.',
						'text',
						'nelio-ab-testing'
					),
					statusName
				),
				knownProduct: sprintf(
					/* translators: 1 -> product name, 2 -> woocommerce status */
					_x(
						'The status of an order containing the product %1$s is set to %2$s.',
						'text',
						'nelio-ab-testing'
					),
					'%s',
					statusName
				),
			};
	} //end switch
} //end getLabelForStatus()
